.footer {
  background-color: $base-black-font;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 55px;
  padding-bottom: 55px;

  @media (max-width: 1023px) {
    padding: 50px 45px 65px 50px;
  }

  @media (max-width: 767px) {
    padding: 0;
    padding-bottom: 35px;
  }
}

.footer__logo-link {
  display: block;
  width: 215px;
  height: 55px;
  margin-bottom: 30px;

  @media (max-width: 1023px) {
    margin-bottom: 0;
  }
  
  @media (max-width: 767px) {
    margin-bottom: 15px;
  }
}

.footer__footer-wrapper {
  width: 63.5%;
  display: flex;
  align-items: flex-start;
  gap: 0;

  @media (max-width: 1400px) {
    width: 80%;
  }

  @media (max-width: 1023px) {
    flex-direction: column;
    width: 100%;
  }

  @media (max-width: 767px) {
    padding: 0;
    width: 100%;
  }
}

.footer__links-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: start;

  @media (max-width: 1023px) {
    width: 100%;
    gap: 120px;
  }

  @media (max-width: 767px) {
    display: block;
  }
}
